import React, { useState } from "react";
import { Container, Row, Col, Form, Button } from "react-bootstrap";

const ForgotPassword = () => {
  const loginPage = {
    display: "flex",
    alignItems: "center",
    height: "100vh",
    width: "100%",
    backgroundImage:
      "url('https://img.freepik.com/free-photo/travel-kit-elements-with-copy-space_23-2148315770.jpg?w=1060&t=st=1686452436~exp=1686453036~hmac=3f6272dd479b20ee162c25394dfd6af7cb6d38bb4848bb0d04fafbf61578994b')",
    backgroundSize: "cover",
  };

  const rightColumn = {
    padding: 2,
  };
  const forgotPasswordContainer = {
    padding: 4,
    borderRadius: 10,
    backgroundColor: "#ffffff",
  };
  const formInput = {
    width: "100%",
    marginBottom: 2,
  };
  const submitButton = {
    width: "100%",
    backgroundColor: "#6cc2d5",
    color: "#ffffff",
    borderRadius: 4,
    marginTop: 2,
  };
  const title = {
    fontSize: 40,
    color: "#333",
    margin: 20,
    fontWeight: "bold",
    fontFamily: "Arial, sans-serif",
    textShadow: "2px 2px 4px rgba(0, 0, 0, 0.3)",
    letterSpacing: 1,
  };

  const [email, setEmail] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log("Forgot password form submitted with email:", email);

    setEmail("");
  };

  return (
    <Container style={loginPage}>
      <Row>
        <Col xs={12} sm={6}>
          <div style={forgotPasswordContainer}>
            <h1 style={title}>Reset Password</h1>
            <Form onSubmit={handleSubmit}>
              <Form.Group controlId="email">
                <Form.Label>Email address</Form.Label>
                <Form.Control
                  style={formInput}
                  type="email"
                  placeholder="Enter email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </Form.Group>
              <Button
                style={submitButton}
                variant="primary"
                type="submit"
              >
                Reset Password
              </Button>
            </Form>
          </div>
        </Col>
        <Col xs={12} sm={6} style={rightColumn}></Col>
      </Row>
    </Container>
  );
};

export default ForgotPassword;
