import React from 'react';

const ItineraryPage = ({ itinerary }) => {
  return (
    <div>
      <h2>Itinerary</h2>
      {itinerary.map((item, index) => (
        <p key={index}>
          {item.title} - Date: {item.date}, Time: {item.time}
        </p>
      ))}
    </div>
  );
};

export default ItineraryPage;
 